.hero-page-about {
  height: 80vh;
  width: 100%;
  background: #eafbf1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.hero-page-about > p {
  text-align: center;
  font-size: large;
}

.top {
  height: 10px;
  width: 100px;
  background: #0e347e !important;
  margin-bottom: 20px;
}

.box {
  position: absolute;
  width: 86.96px;
  height: 84.19px;
  left: 10px;
  bottom: 20%;
  background: #fff8e5;
  border: 8px solid #ffffff;
  box-sizing: border-box;
  border-radius: 4px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.action-box {
  background: #fff8e5;
  border-radius: 8px;
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.img-data {
  width: 100%;
}
